input[type="text"],
input[type="password"] {
    width: 250px;
    height: 25px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    display: block;
    margin: auto;
    padding: 15px;
}

input[type="submit"] {
    margin-top: 10px;
    cursor: pointer;
    font-size: 15px;
    background: #01d28e;
    border: 1px solid #01d28e;
    color: #fff;
    padding: 10px 20px;
}

input[type="submit"]:hover {
    background: #6cf0c2;
}

.input-container {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin: 10px;
}